.catalog__info--olympia .catalog__info-button,
.catalog__info--odissey .catalog__info-button,
.catalog__info--minion .catalog__info-button {
  margin-bottom: 17px;
}
@media (max-width: 767px) {
  .catalog__info--olympia .catalog__info-button,
.catalog__info--odissey .catalog__info-button,
.catalog__info--minion .catalog__info-button {
    margin-bottom: 52px;
  }
}

@media (max-width: 767px) {
  .catalog__info--olympia .catalog__info-button {
    margin-bottom: 15px;
  }
}

.reservation {
  position: relative;
}

.reservation__container {
  padding: 50px 163px 50px;
  display: grid;
  grid-template-columns: 1fr 403px;
  gap: 144px;
}
@media (max-width: 1023px) {
  .reservation__container {
    grid-template-columns: 1fr;
    padding: 100px 175px 100px 175px;
  }
}
.reservation img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: -10;
}

.reservation__description {
  align-self: center;
}

.reservation__description h2 {
  color: #011c40;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 40px;
  font-weight: 700;
  line-height: 100%; /* 60px */
  margin: 0;
  margin-top: 20px;
}
@media (max-width: 1023px) {
  .reservation__description h2 {
    font-size: 34px;
  }
}
@media (max-width: 767px) {
  .reservation__description h2 {
    font-size: 24px;
    line-height: 130%; /* 31.2px */
  }
}

.reservation__description p {
  color: #011c40;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%; /* 27px */
  display: block;
  max-width: 352px;
}
@media (max-width: 767px) {
  .reservation__description p {
    font-size: 16px;
    line-height: 140%; /* 22.4px */
  }
}

.reservation__form {
  background-color: #011c40;
  padding: 25px 50px 50px;
}
@media (max-width: 1023px) {
  .reservation__form {
    max-width: none;
  }
}
.reservation__form h3 {
  color: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 150%; /* 27px */
  margin-bottom: 28px;
}
@media (max-width: 767px) {
  .reservation__form h3 {
    line-height: 140%; /* 25.2px */
  }
}

form {
  max-width: 403px;
}

form input {
  display: block;
  background-color: #f9fbfd;
  border: none;
  width: 100%;
  min-height: 43px;
  margin-bottom: 20px;
  padding: 11px 16px;
  color: rgba(1, 28, 64, 0.7);
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 21px */
}
form input::placeholder {
  color: #011c40;
}

.form__agreement {
  position: relative;
  min-height: 18px;
  margin-bottom: 28px;
  margin-top: 22px;
}
.form__agreement label {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.form__agreement-check-mark {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__agreement-check-mark {
    width: 20px;
    height: 20px;
  }
}

.form__agreement input:checked + .form__agreement-check-mark {
  background-image: url("../img/svg/agree.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.form__agreement-description {
  display: block;
  align-self: center;
  padding-left: 23px;
  color: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 130%; /* 13px */
}
@media (max-width: 767px) {
  .form__agreement-description {
    padding-left: 28px;
    line-height: 120%;
  }
}

.form__button {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10.4px;
  background-color: #f9fbfd;
  color: #011c40;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 150%; /* 21px */
}
